
  import { defineComponent } from 'vue';
import { store } from '../../store/store';
  
  export default defineComponent({
    data() {
      return {
        password: '',
        passwordError: '',
      };
    },
  
    methods: {
      validatePass() {
        if (this.password.length <= 9) {
          this.passwordError = 'Password may have between 10 and 25 characters';
        } else {
          this.passwordError = '';
        }
        store.password = this.password
      },
  
      resetInput() {
        this.passwordError = '';
      },
    },
  });
  